const dataTrasporter = require('../core/data-trasporter.js').default;

const DOWLOAD_MODELS = "DOWLOAD_MODELS";
const DOWLOAD_MODELS_SUCCESS = "DOWLOAD_MODELS";
const DOWLOAD_MODELS_FAILURE = "DOWLOAD_MODELS";

const bodyFormData = new FormData();
bodyFormData.set('api_token','wTCDQleCEWCYrrVu');
export function download_models(){
	// debugger;
	return dataTrasporter.post('objects',  bodyFormData)
		.then(result=>{
			if(result.error){
				throw new Error("Error in downloading objects");
			}
			return {
				type: DOWLOAD_MODELS,
				payload: result.data.result
			};
		})
		.catch(error=>{
			console.log(error);
		});
}

export default function (state = [], action){
	// debugger;
	switch (action.type) {
		case DOWLOAD_MODELS:
			return Object.assign({}, {info_models: action.payload });
		default:
			return state
	}
}


