import React,{Component} from 'react';

import Head_panel  from './head_panel.js';
import Details  from './details';
import Offers  from './offer';
import $ from "jquery";
import {connect} from 'react-redux';

class right_content extends Component{

	constructor (props) {
		super (props);
		this.onModelSelected = this.onModelSelected.bind(this);
		this.state = {selectedModel:null};
	}

	onModelSelected(model){
		this.setState({selectedModel: model});
		var offer = $("#model"+model.id);
		var img = offer.find('.face').children('img').attr('src');
		var name = offer.find('.nickname').text();
		var age = offer.find('.age').text();
		var link = offer.find('.href').text();
		var title = offer.find('.title').text();
		var rating = offer.find('.rating').text();
		var more_details = offer.find('.more_details').clone();
		var other = offer.find('.other').clone();

			$('.rating, .more_details, .other, .nikname, .cost, .top_description').addClass('hidden');
			$('.details').find('.present').children().remove();
			$('.details').find('.present').append('<img src="'+model.image+'" alt="" />');
			$('.details').find('.present').children('img').first().addClass('active');

			var other_img = offer.find('.other_img').find('img');
			if(other_img.length <= 1) {
				$('.slider').addClass('hidden_slider');
			}
			else {
				$('.slider').removeClass('hidden_slider');
				var other_img = offer.find('.other_img').children('li').each(function() {
				var a = $(this).text();
					$('.details').find('.present img.active').after('<img src="'+a+'" alt="" />');
				});
			}
			setTimeout(function (){
				$('.details').find('.nikname').text(name);
				$('.details').find('.cost').text(age);
				$('.details').find('.btn_review').attr('href',link);
				$('.details').find('.top_description').text(title);
				$('.details .rating li.active').removeClass('active');
				for(var i = 1; i <= rating; i++) {
					$('.details .rating li:nth-child('+i+')').addClass('active');
				}

				$('.details .more_details, .details .other').remove();
				$('.details .head').after(more_details);
				$('.details .more_details').after(other);
			},430);
			setTimeout(function (){
				$('.rating, .more_details, .other, .nikname, .cost, .top_description').removeClass('hidden');
			},500)
	}

	render(){
		const infoModels = this.props.info_models;

		return (
			<div className="right_block">
			<div className={`preset_panel ${this.state.selectedModel ? "review": ""}`}>

			<div className="choice_panel">
				<Head_panel />
				{ infoModels && infoModels.map(model=><Offers key={model.id} model={model} onModelSelect={this.onModelSelected}/>) }
			</div>

				<Details selectedModel={this.state.selectedModels}/>
			</div>
			</div>

			);
		}

	}

	function mapStateToProps(state){
		return {
			info_models: state.info_models.info_models
		};
	}
	export default connect(mapStateToProps)(right_content);


