import React,{Component} from 'react';
import $ from "jquery";
import {connect} from 'react-redux';


class offer extends Component{	

	constructor(props){
		super(props);
	}

	render(){
		let model = this.props.model;
		// console.log(model);
		return (
			
				<div id={"model"+model.id} className="offer" data-categoria={model.category} onClick={()=>this.props.onModelSelect(model)}>
					<div className="face">
						<img src={model.image} data-other={model.image_second} alt="" />
					</div>

					<div className="short_description">
						<div className="name">
							<span className="nickname">	{model.id}, </span>
							<span className="age">{model.age}</span>
							<span className="title">
								{model.bio} 
							</span>
							<span className="href">single_page.html</span>
							<span className="rating">{model.rating}</span>
							<ul className="other_img">
								<li>
									{model.image_second}
								</li>
								
							</ul>
							<ul className="more_details">
								<li>
									<span>
										sex
									</span>
									<span className="title">
										{model.sex}
									</span>

								</li>

								<li>
									<span>
										Preference
									</span>
									<span className="title">
										{model.preference}
									</span>

								</li>

								<li>
									<span>
										Hair
									</span>
									<span className="title">
										{model.hair}
									</span>

								</li>

								<li>
									<span>
										Breast
									</span>
									<span className="title">
										{model.breast}
									</span>

								</li>

								<li>
									<span>
										Category
									</span>
									<span className="title">
										{model.category}
									</span>

								</li>

								<li>
									<span>
										Langueage
									</span>
									<span className="title">
										{model.languages}
									</span>

								</li>

								<li>
									<span>
										Ethnicity
									</span>
									<span className="title">
										{model.ethnicity}
									</span>

								</li>

								<li>
									<span>
										Coutry
									</span>
									<span className="title">
										{model.country}
									</span>

								</li>
							</ul>

							<div className="other">
								<p className="text">
									<span>Turnons</span>
									<span className="title">
										{model.turnson}

									</span>
								</p>

								<p className="text">
									<span>willingnesses</span>
									<span className="title">
										{model.willingnesses} 

									</span>
								</p>

								<p className="text">
									<span>appearances</span>
									<span className="title">
										{model.appearances} 

									</span>
								</p>
								
								
							</div>

						</div>
					</div>
				</div>			
			);
	}

}

export default offer;



