import React,{Component} from 'react';
import logo from '../../view/img/logo.svg';
import Mobile_button  from "./mobile_button.js";
import Gender_button  from "./gender_button.js";
import Gender_list  from "./gender_list.js";
import $ from "jquery";

import { Link } from 'react-router-dom';

class header extends Component{

	

	


	componentDidMount() {
		
		$(document).on('click', '.gender_button ', function (){
			$('.gender_list').toggleClass('open');
			$('.preset_panel.review').removeClass('review');
			$('.preset_panel .offer.active').removeClass('active');
		});

		$(document).on('click', '.gender_list li ', function (){
			let data_id = $(this).attr('data-id');
			$(this).closest('ul').find('li.active').removeClass();
			$(this).addClass('active');
			$('.gender_button').removeClass().addClass('gender_button').addClass(data_id);
			$('.gender_list').removeClass('open');
		});

		$(document).on('click', 'section', function (){
			$('.gender_list').removeClass('open');
		});
		
		let a = $('.gender_list').find('li.active').attr('data-id');
		$('.gender_button').addClass(a);
  	}


	render(){
		return (
	
		<header>
			<Mobile_button />
			

			<Link to="/">
				<span className="logo">
					<img src={logo} alt="" />
				</span>
			</Link>


			<div className="gender_wrap">
				<Gender_button />
				<Gender_list />

				
			</div>
		</header>
			);
	}

}
export default header;


