
const dataTrasporter = require('../core/data-trasporter.js').default;

const DOWLOAD_FILTERS = "DOWLOAD_FILTERS";
const DOWLOAD_FILTERS_SUCCESS = "DOWLOAD_FILTERS"
const DOWLOAD_FILTERS_FAILURE = "DOWLOAD_FILTERS"

const bodyFormData = new FormData();
bodyFormData.set('api_token','wTCDQleCEWCYrrVu');

// export function download_filters(){
// 	return {
// 		type: DOWLOAD_FILTERS
// 	}
// }

export function download_filters(){
	// debugger;
	return dataTrasporter.post('filters',  bodyFormData)
		.then(result=>{
			if(result.error){
				throw new Error("Error in downloading objects");
			}
			return {
				type: DOWLOAD_FILTERS,
				payload: result.data.result
			};
		})
		.catch(error=>{
			console.log(error);
		});
}

export default function (state = [], action){
	// debugger;
	switch (action.type) {
		case DOWLOAD_FILTERS:
			return Object.assign({}, {filters: action.payload });
		default:
			return state
	}
}


