import React,{Component} from 'react';

// import Header  from '../header';
import Header  from '../header';
import Main_content  from '../main_content';
import Single  from '../model';
import $ from "jquery";
import axios from 'axios';
import { Switch, Route  } from 'react-router-dom';
import {connect} from 'react-redux';
import {download_filters} from '../../view/js/reduser/filters.js';
import {download_models} from '../../view/js/reduser/models_info.js';



class app extends Component{

	constructor(props) {
		super(props);
		// debugger;
		props.downloadModels();
		props.downloadFilters();
	}

	

	render(){
		return (
			<div>
				<Header />
				<Switch>
					<Route path="/" exact component={ Main_content } />
					<Route path="/model" component={ Single } />
				</Switch>
			</div>

			);
	}

}


const mapDispatchToProps = (dispatch, ownProps) => {
	// debugger;
	return {
		downloadModels: () => {
			dispatch(download_models());
		},
		downloadFilters: () => {
			dispatch(download_filters());
		}
	};

}

function mapStateToProps(state){
	return state;
}
export default connect(mapStateToProps, mapDispatchToProps)(app);


