import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import '../src/view/css/style.css';
import '../src/view/css/media.css';

import { BrowserRouter } from 'react-router-dom';
import App  from './components/main';
import registerServiceWorker from './registerServiceWorker';
import allReducers from './view/js/reduser';

import axios from 'axios';

// debugger;
const promiseMiddleware = require('redux-promise').default;
const store  = createStore(allReducers, applyMiddleware(promiseMiddleware));

// const url = 'http://api.horteca.com/api/object';
// fetch(url)
//   .catch(error => console.log('BAD', error))
//   .then(response => console.log(response));
// debugger;

ReactDOM.render(
	<Provider store= {store}>
		<BrowserRouter>
			<App />
		</BrowserRouter>
	</Provider>,
	document.getElementById('root'));
registerServiceWorker();



