import React,{Component} from 'react';

import $ from "jquery";


class slider extends Component{

	componentDidMount() {



		//slider
		$(document).on('click', '.arrow.next', function (){
			var next_clide = $('.present ').find('img.active').removeClass().next('img').addClass('active');
			if(!next_clide.length) {
				$('.present ').find('img.active').removeClass('active');
				$('.present img').first().addClass('active');
			}
		});

		$(document).on('click', '.arrow.prev', function (){
			var prev_clide = $('.present ').find('img.active').removeClass().prev('img').addClass('active');
			if(!prev_clide.length) {
				$('.present ').find('img.active').removeClass('active');
				$('.present img').last().addClass('active');
			}
		});

		$(document).on('click', '.close', function (){
			$('.preset_panel').removeClass('review');
			$('.preset_panel').find('.offer.active').removeClass('active');
			$('.slider .present img').remove();
		});

		function img(){
			var img_length = $('.present').find('img');
			if(img_length.length > 2) {
				$('.slider').addClass('many_img');
			}
			else {
				$('.slider').removeClass('many_img');
			}
		}
		img()

		
		key_button();
		function key_button(){
			if($('.preset_panel').hasClass('review') || $('section').hasClass('banner')){
				$('html').on('keydown', function (events){
					if (events.keyCode == 39) {
						$('.arrow.next').click();
					} else if (events.keyCode == 37) {
						$('.arrow.prev').click();
					} else if (events.keyCode == 27) {
						$('.close').click()
					}
				});

			}
		}
	}

	render(){
		return (
			<div className="slider">
				<img src={require('../../view/img/close.svg')} alt="" className="close" />
				<img src={require('../../view/img/arrow.svg')} alt="" className="arrow prev" />

				<div className="present">
					<img src={require('../../view/img/i1.jpg')} className="active" alt="" />
					<img src={require('../../view/img/i2.jpg')} className="" alt="" />
					<img src={require('../../view/img/i3.jpg')}  alt="" />
					<img src={require('../../view/img/i4.jpg')}  alt="" />
				</div>

				<img src={require('../../view/img/arrow.svg')} alt="" className="arrow next" />
			</div>
			);
		}

	}
	export default slider;


