import React,{Component} from 'react';
import { connect } from 'react-redux';
import $ from "jquery";
class mobile_button extends Component{

 	componentDidMount() {
		$(document).on('click', '.mobile_button', function (){
			$('.mobile_button, .left_menu').toggleClass('active');
		});
  	}
	render(){
		return (
			<div className="mobile_button" >
				<span></span>
				<span></span>
				<span></span>
			</div>
			);
	}

}
export default mobile_button;


